<template>
  <div class="banner">



        <div class="content">
            <h3>معرفی مناطق گردشگری استان مازندران</h3>
            <p>مکان های جدید را با ما کشف کنید ، ماجراجویی در انتظار است</p>
            <span>طراح و برنامه نویس: علی مقصودی</span>
        </div>



    <div ref="swiper" class="swiper">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, i) in items" :key="i">
          <div style="width: 100vw;height: 100vh">
            <img :src="item.src" style="width: 100%;height: 100%"/>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
    import Swiper from 'swiper';
    import { Navigation, Pagination, Autoplay } from 'swiper/modules';
    import 'swiper/css';
    import 'swiper/css/navigation';
    import 'swiper/css/pagination';

    export default {
        name: "BannerMain",
        data: () => ({
            swiper: null,
            items: [
                {src: 'https://www.iraneean.ir/wp-content/uploads/lhcknvhk.jpg'},
                {src: 'https://kite.ir/UploadData/Other/shakhes.jpg_mxbty5ck.k2m.jpg'},
                {src: 'https://harfonline.ir/wp-content/uploads/2022/07/%D8%AC%D8%A7%D8%AF%D9%87-%DA%86%D8%A7%D9%84%D9%88%D8%B3.jpg?v=1657611061'},
                {src: 'https://cdn.downloadefilm.ir/images/9ca9ced0-b1aa-11ee-97fa-916375a78818.jpg'},
                {src: 'https://cdn.downloadefilm.ir/images/63371bc0-8edc-11ee-9af2-9f16c0191b5e.jpg'},
            ]
        }),
        mounted() {
            this.swiper = new Swiper(this.$refs.swiper, {
                modules: [Navigation, Pagination, Autoplay],
                spaceBetween: 20,
                speed: 600,
                loop:true,
                autoplay: {
                    delay: 2500,
                    disableOnInteraction: false,
                }
            });
        },
        methods: {
            swiperNext() {
                this.swiper.slideNext();
            },
            swiperPrev() {
                this.swiper.slidePrev();
            }
        }
    }
</script>

<style scoped>
  .banner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content{
    text-align: center;
    position: absolute;
    z-index: 10;
    background-color: rgba(255,255,255,.4);
    padding: 2rem 2.5rem;
    border-radius: 1.4rem;
    backdrop-filter: blur(5px);
  }

  .content h3{
    font-size: 4.5rem;
    color:var(--orange);
    text-transform: uppercase;
    text-shadow: 0 .3rem .5rem rgba(0,0,0,.1);
  }

  .content p{
    font-size: 2.5rem;
    color:#fff;
    padding:.5rem 0;
    margin: 8px 0;
  }

  .content span{
    font-size: 1.5rem;
    color:#000;
    opacity: .7;
    padding:1rem 0;
  }

  .swiper-slide {
    position: relative;
  }

  .swiper-slide:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
  }
</style>